(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('cardsBox', cardsBox);

  /** @ngInject */
  function cardsBox() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/calendar/advancedEdit/cardsBox/cardsBox.html',
      controller: CardsBoxController,
      controllerAs: 'cardsBox',
      bindToController: true,
      replace: true,
      scope: {
        cards: '=',
        teamName: '=',
        teamId: '=',
        matchId: '=',
        cardsKey: '@',
        autocomplete: '='
      }
    };

    return directive;

    /** @ngInject */
    function CardsBoxController(utils, $q, matches, $scope, teams) {
      var vm = this;

      vm.cardTypes = utils.cardTypes();
      vm.disqualificationDays = utils.scores();

      vm.setLoading = function (res) {
        vm.loading = true;
        return $q.resolve(res);
      }

      vm.unsetLoading = function (res) {
        vm.loading = false;
        return $q.resolve(res);
      }

      vm.createCard = function () {
        vm.setLoading(vm.form)
          .then(matches.form2Card)
          .then(function (cardData) {
            return matches.createCard(vm.matchId, cardData, vm.cardsKey);
          })
          .then(vm.resetForm)
          // manually update the cards list
          .then(function(newCard) {
            var toUpdate = vm.cards.filter(
              function(card) { return card.cardId === newCard.cardId });
            if (toUpdate.length === 0)
              vm.cards.push(newCard);
          })
          .finally(vm.unsetLoading);
      }

      vm.destroyCard = function (card) {
        vm.setLoading()
          .then(function () {
            return matches.destroyMatchCard(vm.matchId, card.cardId, vm.cardsKey)
          })
          // manually update the cards list
          .then(function() {
            vm.cards = vm.cards.filter(
              function(c) { return c.cardId !== card.cardId });
          })
          .finally(vm.unsetLoading);
      }

      vm.resetFormModel = function () {
        vm.form = { teamId: vm.teamId, goals: 1, playId: 0 }
      }

      vm.resetFormFields = function () {
        $scope['createCardForm' + vm.teamId].$setPristine();
        $scope['createCardForm' + vm.teamId].$setUntouched();
      }

      vm.resetForm = function (res) {
        vm.resetFormModel();
        vm.resetFormFields();
        return res;
      }

      // GetterSetter function - see angular docs for ng-model-options
      vm.typeaheadName = function (val) {
        if(arguments.length){
          if(angular.isString(val)){
            vm.form.name = val;
            vm.form.playId = 0;
          }
          else if(angular.isObject(val)){
            vm.populateForm(val);
          }
        }
        else{
          return vm.form.name;
        }
      }

      // GetterSetter function - see angular docs for ng-model-options
      vm.typeaheadSurname = function (val) {
        if(arguments.length){
          if(angular.isString(val)){
            vm.form.surname = val;
            vm.form.playId = 0;
          }
          else if(angular.isObject(val)){
            vm.populateForm(val);
          }
        }
        else{
          return vm.form.surname;
        }
      }

      vm.populateForm = function (obj) {
        if(angular.isString(obj.name)) vm.form.name = obj.name;
        if(angular.isString(obj.surname)) vm.form.surname = obj.surname;
        if(angular.isNumber(obj.teamId)) vm.form.teamId = obj.teamId;
        if(angular.isNumber(obj.playId)) vm.form.playId = obj.playId;
      }

      teams.listPlayers(vm.teamId)
        .then(function (res) {
          vm.autocomplete = [];
          if(angular.isArray(res)){
            angular.forEach(res, function (el) {
              vm.autocomplete.push({
                playId: el.playId,
                name: el.playFirstname,
                surname: el.playSurname,
                teamId: el.playTeamId
              });
            });
          }
        });

      vm.resetFormModel();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('scorersBox', scorersBox);

  /** @ngInject */
  function scorersBox() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/calendar/advancedEdit/scorersBox/scorersBox.html',
      controller: ScorersBoxController,
      controllerAs: 'scorersBox',
      bindToController: true,
      replace: true,
      scope: {
        scorers: '=',
        teamName: '=',
        teamId: '=',
        matchId: '=',
        autocomplete: '=',
        scorersKey: '@'
      }
    };

    return directive;

    /** @ngInject */
    function ScorersBoxController(utils, $q, matches, teams, $scope, models) {
      var vm = this;
      vm.scores = utils.scores();
      vm.scores.splice(0,1);

      vm.setLoading = function (res) {
        vm.loading = true;
        return $q.resolve(res);
      }

      vm.unsetLoading = function (res) {
        vm.loading = false;
        return $q.resolve(res);
      }

      vm.createScorer = function () {
        vm.setLoading(vm.form)
          .then(matches.form2Scorer)
          .then(function (scorerData) {
            return matches.createScorer(vm.matchId, scorerData, vm.scorersKey);
          })
          .then(vm.resetForm)
          // manually update the scorers list because for some reasons
          // the binding with variable scorers has been lost
          .then(function(newScorer) {
              var toUpdate = vm.scorers.filter(
                function(scorer) { return scorer.playId === newScorer.playId });
              if (toUpdate.length !== 0)
                toUpdate[0].goal = newScorer.goal;
              else vm.scorers.push(newScorer);
          })
          .finally(vm.unsetLoading);
      }

      vm.destroyScorer = function (scorer) {
        vm.setLoading()
          .then(function () {
            return matches.destroyScorer(vm.matchId, scorer.playId, vm.scorersKey)
          })
          // manually update the scorers list because for some reasons
          // the binding with variable scorers has been lost
          .then(function() {
            var toRemoveIndex = vm.scorers
              .map(function(actual, index) {
                return actual.playId === scorer.playId ? index : 0;
            }).reduce(
              function(a, b) { return a + b; }, 0);

            vm.scorers.splice(toRemoveIndex, 1);
          })
          .finally(vm.unsetLoading);
      }

      vm.resetFormModel = function () {
        vm.form = { teamId: vm.teamId, goals: 1, playId: 0 }
      }

      vm.resetFormFields = function () {
        $scope['createScorerForm' + vm.teamId].$setPristine();
        $scope['createScorerForm' + vm.teamId].$setUntouched();
      }

      vm.resetForm = function (res) {
        vm.resetFormModel();
        vm.resetFormFields();
        return res;
      }

      // GetterSetter function - see angular docs for ng-model-options
      vm.typeaheadName = function (val) {
        if(arguments.length){
          if(angular.isString(val)){
            vm.form.name = val;
            vm.form.playId = 0;
          }
          else if(angular.isObject(val)){
            vm.populateForm(val);
          }
        }
        else{
          return vm.form.name;
        }
      }

      // GetterSetter function - see angular docs for ng-model-options
      vm.typeaheadSurname = function (val) {
        if(arguments.length){
          if(angular.isString(val)){
            vm.form.surname = val;
            vm.form.playId = 0;
          }
          else if(angular.isObject(val)){
            vm.populateForm(val);
          }
        }
        else{
          return vm.form.surname;
        }
      }

      vm.populateForm = function (obj) {
        if(angular.isString(obj.name)) vm.form.name = obj.name;
        if(angular.isString(obj.surname)) vm.form.surname = obj.surname;
        if(angular.isNumber(obj.teamId)) vm.form.teamId = obj.teamId;
        if(angular.isNumber(obj.playId)) vm.form.playId = obj.playId;
      }

      vm.resetFormModel();
    }
  }

})();

