(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('teamShow', teamShow);

  /** @ngInject */
  function teamShow() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/teams/show/show.html',
      controller: TeamShowController,
      controllerAs: 'teamShow',
      bindToController: true,
      replace: true,
      link: TeamShowLink,
      scope: {
        team: '=teamShow'
      }
    };

    return directive;

    /** @ngInject */
    function TeamShowController($uibModal, teams, models) {
      var vm = this;

      vm.destroy = function() {

        $uibModal.open({
            templateUrl: 'app/pages/teams/destroy/destroy.html',
            controller: 'TeamDestroyController',
            controllerAs: 'teamDestroy',
            windowClass: 'modal-danger'
          }).result
          .then(function (res) {
            if(res){
              if (vm.team.teamTemp === 1) {
                teams.destroy(vm.team);
              } else {
                teams.destroyTemp(vm.team);
              }
            }
          })
      }

      vm.models = models;
    }

    /** @ngInject */
    function TeamShowLink(scope, el, attrs, vm){
      el.hover(
        function () {
          scope.$apply(function () {
            vm.showControls = true;
          })
        },
        function () {
          scope.$apply(function () {
            vm.showControls = false;
          })
        }
      );
    }
  }

})();

