(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('teamEdit', teamEdit);

  /** @ngInject */
  function teamEdit() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/teams/edit/edit.html',
      controller: TeamEditController,
      controllerAs: 'teamEdit',
      link: TeamEditLink,
      bindToController: true,
      replace: true,
      scope: {
        team: '=teamEdit'
      }
    };

    return directive;

    /** @ngInject */
    function TeamEditController(models, $q, teams, $window, utils, $uibModal) {
      var vm = this;

      vm.models = models;

      vm.teamFormulas = utils.teamOptions();

      vm.week = [
        {
          id: 1,
          value: "Lunedì"
        },
        {
          id: 2,
          value: "Martedì"
        },
        {
          id: 3,
          value: "Mercoledì"
        },
        {
          id: 4,
          value: "Giovedì"
        },
        {
          id: 5,
          value: "Venerdì"
        },
        {
          id: 6,
          value: "Sabato"
        },
        {
          id: 7,
          value: "Domenica"
        }
      ]

      vm.setLoading = function (res) {
        vm.loading = true;
        return $q.resolve(res);
      }

      vm.unsetLoading = function (res) {
        vm.loading = false;
        return $q.resolve(res);
      }

      vm.loadtmp = function() {
        return vm.setLoading()
          .then(function () {
            var span = angular.element('#'+vm.team.teamId+'-upload-file-info');
            span.html('Immagine caricata, premere Salva per confermare');
          })
          .finally(vm.unsetLoading);
      }

      vm.updatePlace = function (luogo) {
        $uibModal.open({
          templateUrl: 'app/pages/teams/edit/updatePlace.html',
          controller: 'TeamUpdatePlaceController',
          controllerAs: 'teamUpdatePlace',
          windowClass: 'modal-warning',
          resolve: {
            luogo: function() {
              return luogo;
            }
          }
        }).result
        .then(function (res) {
          if(res){
            return vm.setLoading(vm.team.teamId, res)
              .then(teams.updatePlace(vm.team.teamId, res))
              .then(function () {
                return teams.show(vm.team.teamId);
              })
              .finally(vm.unsetLoading);
          }
        })

      }

      vm.submit = function () {
        var updateplace = vm.form.place!=vm.team.teamPlace;
        return vm.setLoading(vm.form)
          .then(teams.getTokenS3)
          .then(function(data){
            var angularFile = angular.element('#teamEditForm'+vm.team.teamId+'-teamUrlLogo');
            var file = angularFile[0].files[0];

            if (file) {
              var deferred = $q.defer();
              var credentials = {accessKeyId: data.data[0].keyid, secretAccessKey: data.data[0].secretid, sessionToken: data.data[0].session_token};
              AWS.config.update(credentials);
              AWS.config.region = 'eu-west-1';
              AWS.config.sslEnabled = true;
              var bucket = new AWS.S3({params:{Bucket:'com.codeploy.frispo.image.teams/original'}});
              var fileName = "";
              if (file) fileName = angularFile[0].files[0].name;
              var ts = new Date().getTime();
              var fileNameEscape = fileName.replace(/\s+/g,'_');
              vm.form.teamUrlLogo = ts +  "-" + fileNameEscape;
              if(file){
                var params = {Key: '' + ts+"-" + fileNameEscape, ContentType: file.type, Body: file };
                bucket.upload(params, function(err, data){
                  if(data){
                    deferred.resolve(data);
                  }else if(err){
                    deferred.reject(err);
                  }
                })
              }else{
                deferred.reject(false);
              }
              return deferred.promise;
            }
          })
          .then(function(){
            return teams.form2Team(vm.form)
          })
          .then(function (teamData) {
            return teams.update(vm.team.teamId, teamData);
          })
          .then(function () {
            var span = angular.element('#'+vm.team.teamId+'-upload-file-info');
            span.html('');
          })
          .then(teams.list)
          .then(teams.listTemp)
          .finally(vm.unsetLoading);
      }

      vm.showLogo = function (linkLogo){
        $window.open(linkLogo, '_blank');
      }

    }

    function TeamEditLink(scope, el, attrs, vm) {
      vm.cancel = function () {
        el.find('.accordion-body').collapse('hide');
      }
    }
  }

})();

