(function() { 'use strict'

  angular.module('frispo')
    .controller('PressLoadController', PressLoadController);

  /** @ngInject */
  function PressLoadController($uibModalInstance, models, press, utils) {

    var vm = this; vm.models = models;
    vm.multiSelectTranslation = utils.istevenMultiSelectTranslation();
    vm.events = angular.copy(models.events);
    vm.fileText = "";

    function getAWSBucket() {
      return press.getTokenS3().then(function(res) {
          var credentials = {
            accessKeyId: res.data[0].keyid,
            secretAccessKey: res.data[0].secretid,
            sessionToken: res.data[0].session_token
          };
          AWS.config.update(credentials);
          AWS.config.region = 'eu-west-1';
          AWS.config.sslEnabled = true;
          return new AWS.S3({
            params: { Bucket: 'com.iaetp.frispo' }
          });
        },
        function(err) { vm.uploadArray.press_url = err; });
    }

    function loadComunicate(file, bucket) {
      var params = {
        ContentType: file.type, Body: file,
        Key: 'comunicati/' +
          new Date().getTime() + file.name.replace(/\s+/g,'_')
      };
      bucket.upload(params, function (err, data) {
        vm.uploadArray.press_url = data ? data.Location : err;
        vm.uploadArray.press_name = vm.form.name;
        vm.uploadArray.press_events = vm.form.events;

        vm.uploadArray.press_events.forEach(function(event) {
          event.pres_even_id = event.evenId;
          delete event.evenId;
        });
        $uibModalInstance.close(vm.uploadArray);
      });
    }

    vm.ok = function () {
      vm.uploadArray = {};
      var angularFile = angular.element('#pressLoadForm-file');
      var file = angularFile[0].files[0];

      if (file) getAWSBucket().then(
        function(bucket) { loadComunicate(file, bucket); });
    }

    vm.cancel = function () { $uibModalInstance.dismiss(); }
  }
})();

