(function() { 'use strict';

  angular.module('frispo')
    .controller('MovePlayersController', MovePlayersController);

  /** @ngInject */
  function MovePlayersController(frispoApi, resPipe, errorManager, models, $log, $uibModal) {

    var vm                = this;             // component variable
    vm.models             = models;
    vm.loading            = false;
    vm.selectedEventFrom  = null;
    vm.selectedEventTo    = null;
    vm.currentTeamsFrom   = [];
    vm.currentTeamsTo     = [];
    vm.selectedTeamFrom   = null;
    vm.selectedTeamTo     = null;
    vm.currentPlayerList  = [];
    vm.selectedPlayers    = [];

    vm.selectEventFrom = function(event) {
      vm.selectedEventFrom = event;           // reset
      vm.currentTeamsFrom  = [];
      vm.selectedTeamFrom  = null;
      vm.currentPlayerList = [];

      frispoApi.listTeams(event.evenId)       // load
        .then(resPipe.parseResponse)
        .then(resPipe.camelize)
        .then(function(teams) { vm.currentTeamsFrom = teams; })
        .catch(errorManager);
    }
    vm.selectTeam = function(team) {
      vm.selectedTeamFrom = team;
      frispoApi.listPlayers(team.teamId)
        .then(resPipe.parseResponse)
        .then(resPipe.camelize)
        .then(resPipe.updatePlayers)
        .then(function(players) { vm.currentPlayerList = players; })
        .catch(errorManager);
    }
    vm.selectEventTo = function(event) {
      vm.selectedEventTo = event;
      frispoApi.listTeams(event.evenId)
        .then(resPipe.parseResponse)
        .then(resPipe.camelize)
        .then(function(teams) { vm.currentTeamsTo = teams; })
        .catch(errorManager);
    }

    vm.getPlayerList = function() {
      return vm.selectedPlayers
        .filter(function(ele) { return ele !== false; });
    }

    function getFormData() {
      return { all_player: 0,
        event_id_from: vm.selectedEventFrom.evenId,
        event_id_to: vm.selectedEventTo.evenId,
        list_player: vm.getPlayerList(),
        team_id_from: vm.selectedTeamFrom.teamId,
        team_id_to: parseInt(vm.selectedTeamTo)
      };
    }

    vm.showConfirmPopup = function(op) {
      $uibModal.open({
        templateUrl: 'app/pages/playersMove/confirmPopup/confirm.html',
        controller: 'MovePlayerConfirmController',
        controllerAs: 'confirmPopup'
      })
      .result.then(op === 'copy' ? copyPlayers : movePlayers)
    }
    function movePlayers() {
      frispoApi.movePlayers(getFormData()).then(function(res) {
        if (res.status_code === 200) {
          vm.selectedPlayers = [];
          vm.selectTeam(vm.selectedTeamFrom);
        } else { $log.error(res.msg) }
      });
    }
    function copyPlayers() {
      frispoApi.copyPlayers(getFormData())
        .then(function(res) {
          if (res.status_code === 200) {
            vm.selectedPlayers = [];
            vm.selectTeam(vm.selectedTeamFrom);
          } else { $log.error(res.msg) }
      });
    }
    vm.checkAll = function() {
      vm.selectedPlayers = vm.currentPlayerList
        .map(function(player) { return player.playId });
    }
  }
})();

(function() { 'use strict';

  angular.module('frispo')
    .controller('PlayersController', PlayersController);

  /** @ngInject */
  function PlayersController(models, $scope, $q, $uibModal, teams, objToCsv) {

    var vm = this; vm.models = models; vm.loading = false;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // team info to fill up the team select options
    $scope.$watch(vm.getCurrentEvent, loadEventTeams, true);

    function loadEventTeams(currentEvent) {
      if (!vm.loading && currentEvent &&
         (!currentEvent.teams || !currentEvent.currentTeam)) {
        vm.setLoading(currentEvent.evenId)     // Set a spinning loader on the page
          .then(teams.list)                    // Get the event teams (API call) to fill up the select options
          .then(vm.updateTeam)                 // Update the match day
          .finally(vm.unsetLoading);           // Remove the loader from the page
      }
    }

    // Update the team (it's been selected now)
    vm.updateTeam = function () {
      // No current event selected, or no teams yet -> noop
      if(!models.currentEvent || !models.currentEvent.teams || models.currentEvent.teams.length === 0){
        return;
      }

      // No day selected - check if the event has just changed
      if(!models.currentEvent.currentTeam){
        // An event has been selected -> use it's first available team
        models.currentEvent.currentTeam = models.currentEvent.teams[0];
      }
      // Current day selected -> manually find and copy the real team from models (ng-options creates a new object)
      else{
        for (var i = 0; i < models.currentEvent.teams.length; i++) {
          if(models.currentEvent.teams[i].teamId === vm.models.currentEvent.currentTeam.teamId){
            vm.models.currentEvent.currentTeam = models.currentEvent.teams[i];
            break;
          }
        }
      }

      return vm
        .setLoading()
        .then(teams.listPlayers)
        .finally(vm.unsetLoading);
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.createNewPlayer = function () {
      $uibModal.open({
          templateUrl: 'app/pages/players/create/create.html',
          controller: 'PlayerCreateController',
          controllerAs: 'playerCreate',
          windowClass: 'modal-success'
        }).result
        .then(function (res) {
          if(angular.isDefined(res)){
            vm.setLoading(res)
              .then(teams.form2Player)
              .then(teams.createPlayer)
              .finally(vm.unsetLoading);
          }
        })
    }

    vm.exportData = function (data) {

    var csv = objToCsv(data.map(function(player) {
        return {
          Nome: player.playFirstname,
          Cognome: player.playSurname,
          Tessera: player.playTessera,
          Recapito: player.playRecapito
        }
      }));
      var blob = new Blob([csv], { type: 'text/csv' })
      saveAs(blob, "Giocatori.csv");
    };

    function init() {
      loadEventTeams(vm.getCurrentEvent());
    }
    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .controller('ManagerMatchesController', ManagerMatchesController);

  /** @ngInject */
  function ManagerMatchesController(models, events, matches, $q, $uibModal, teams) {
    var vm = this;
    vm.models = models;

    // Update the current event (it's been selected now)
    vm.updateCurrentEvent = function () {
      // No current event selected -> noop
      if(!models.currentEvent) return;

      // Current event selected -> manually find and copy the real event from models (ng-options creates a new object)
      for (var i = 0; i < models.currentUser.managedEvents.length; i++) {
        if(models.currentUser.managedEvents[i].evenId === models.currentEvent.evenId){
          models.currentEvent = models.currentUser.managedEvents[i];
          break;
        }
      }

      return vm
        .setLoading(models.currentEvent.evenId) // Set a spinning loader on the page
        .then(events.show)                      // Get the event info (API call)
        .then(teams.listcalendarteam)                       // Get the event teams (API call), needed for filling up the select options
        .then(vm.updateMatchDay)                // Update the match day
        .finally(vm.unsetLoading);              // Remove the loader from the page
    }

    // Update the match day (it's been selected now)
    vm.updateMatchDay = function () {
      // No current event selected, or no match days yet -> noop
      if(!models.currentEvent || !models.currentEvent.matchDays || models.currentEvent.matchDays.length === 0){
        return;
      }

      // No day selected - check if the event has just changed
      if(!models.currentEvent.currentDay){
        // An event has been selected -> use it's first available day
        models.currentEvent.currentDay = models.currentEvent.matchDays[0];
      }
      // Current day selected -> manually find and copy the real day from models (ng-options creates a new object)
      else{
        for (var i = 0; i < models.currentEvent.matchDays.length; i++) {
          if(models.currentEvent.matchDays[i].matcDayId === vm.models.currentEvent.currentDay.matcDayId){
            vm.models.currentEvent.currentDay = models.currentEvent.matchDays[i];
            break;
          }
        }
      }

      return vm
        .setLoading()
        .then(matches.list)
        .finally(vm.unsetLoading);
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    // API call to retrieve all the events
    events
      .listManaged()
      .then(vm.updateCurrentEvent);
  }
})();

