(function() {
  'use strict';

  angular
    .module('frispo')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(users, uiState, $state, ezfb) {
    var vm = this;
    vm.user = vm.user || {};
    vm.privacy = vm.privacy || {};

    users.onLoggedOut(); // ensure the user is logged out

    // Login with username and password
    vm.login = function () {
      users.login(vm.user)
        .then(function () {
          $state.go('main.access');
        });
    }
    // Login with facebook

    vm.facebookPrivacyAcceptance = function() {
      return uiState.privacyAcceptance;
    }

    vm.loginFacebook = function (evt) {
      evt.preventDefault();
      var privacyObj = { }
      for (var name in vm.privacy) {
        privacyObj[name] = vm.privacy[name] ? 1 : 0;
      }
      if (privacyObj.hasOwnProperty("flag_privacy") ||
          !uiState.privacyAcceptance) {
        users.configureFacebookCallbacks(privacyObj);
        ezfb.login(null, { scope: 'email,public_profile' });
      }
    }
  }
})();


(function() { 'use strict';

  angular.module('frispo')
    .controller('IscrizioniController', IscrizioniController);

  /** @ngInject */
  function IscrizioniController(models, events, $q, $scope, iscrizioni) {

    var vm = this; vm.models = models;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading && currentEvent) {
        events.list().then(vm.updateCurrentEvent);
      }
    }, true);

    vm.updateCurrentEvent = function () {
      if(!models.currentEvent) return;
      for (var i = 0; i < models.events.length; i++) {
        if(models.events[i].evenId === models.currentEvent.evenId){
          models.events[i] = models.currentEvent;
          break;
        }
      }
      return vm.setLoading(models.currentEvent.evenId)
        .then(events.show)
        .then(iscrizioni.list)
        .finally(vm.unsetLoading);
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }
  }
})();

