(function() { 'use strict';

  angular.module('frispo')
    .controller('CalendarController', CalendarController);

  /** @ngInject */
  function CalendarController(models, $scope, objToCsv, matches, $q, $uibModal, teams, arbiters) {

    var vm = this; vm.models = models; vm.loading = false;
    vm.arbiters = [];

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // the calendar if current event has changed
    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading && currentEvent) {
        vm.setLoading(currentEvent.evenId)    // Set a spinning loader on the page
          .then(teams.listcalendarteam)       // Get the event teams (API call), needed for filling up the select options
          .then(vm.updateMatchDay);            // Update the match day

        vm.setLoading(currentEvent.evenId)    // load event arbiters list
          .then(arbiters.list)
          .then(function(list) {
              vm.arbiters = list; $q.resolve(list); })
          .finally(vm.unsetLoading);
      }
    }, true);     // use object equality and not memory address

    vm.exportData = function (data) {

      var csv = objToCsv(data.map(function(match) {
        var csvObj = {
          "Squadra Casa": match.matcTeamA,
          "Squadra Ospite": match.matcTeamB,
          "Goal Casa": match.matcScoreTeamA,
          "Goal Ospiti": match.matcScoreTeamB,
          "Data Partita": match.matcDateMatchPretty,
          Ora: match.matcTime,
          Indirizzo: match.matcAddress,
          Giocata: match.matcFlgPlay === 1 ? 'si' :
            (match.matcFlgPlay === 0 ? 'no' : 'rinviata')
        }
        if (models.currentEvent.evenType == 1)
          csvObj['Girone'] = match.matcNameGroups;
        return csvObj
      }));
      var blob = new Blob([csv], { type: 'text/csv' })
      saveAs(blob, "Partite.csv");
    }

    // Update the match day (it's been selected now)
    vm.updateMatchDay = function () {
      // No current event selected, or no match days yet -> noop
      if(!models.currentEvent || !models.currentEvent.matchDays || models.currentEvent.matchDays.length === 0){
        return;
      }

      // No day selected - check if the event has just changed
      if(!models.currentEvent.currentDay){
        // An event has been selected -> use it's first available day
        models.currentEvent.currentDay = models.currentEvent.matchDays[0];
      }
      // Current day selected -> manually find and copy the real day from models (ng-options creates a new object)
      else{
        for (var i = 0; i < models.currentEvent.matchDays.length; i++) {
          if(models.currentEvent.matchDays[i].matcDayId === vm.models.currentEvent.currentDay.matcDayId){
            vm.models.currentEvent.currentDay = models.currentEvent.matchDays[i];
            break;
          }
        }
      }

      return vm
        .setLoading()
        .then(matches.list)
        .finally(vm.unsetLoading);
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.createNewMatch = function () {
      $uibModal.open({
          templateUrl: 'app/pages/calendar/create/create.html',
          controller: 'MatchCreateController',
          controllerAs: 'matchCreate',
          windowClass: 'modal-success'
        }).result
        .then(function (res) {
          if(angular.isDefined(res)){
            vm.setLoading(res)
              .then(matches.form2Match)
              .then(matches.create)
              .then(matches.list)
              .finally(vm.unsetLoading);
          }
        })
    }

    vm.createNewDay = function () {
      $uibModal.open({
          templateUrl: 'app/pages/calendar/createDay/create.html',
          controller: 'DayCreateController',
          controllerAs: 'dayCreate',
          windowClass: 'modal-success'
        }).result
        .then(function (res) {
          if(angular.isDefined(res)){
            vm.setLoading(res)
              .then(matches.form2Day)
              .then(matches.createDay)
              .then(function (res) {
                models.currentEvent.currentDay = res;
                return res;
              })
              .then(vm.updateMatchDay)
              .finally(vm.unsetLoading);
          }
        })
    }
  }
})();

(function() { 'use strict';

  angular.module('frispo')
    .controller('ArbiterController', ArbiterController);

  /** @ngInject */
  function ArbiterController(models, arbiters, $uibModal, $q, $scope) {

    var vm = this;
    vm.loading = false;
    vm.arbiters = [ ];
    vm.models = models;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading && currentEvent) {
        vm.setLoading(currentEvent.evenId)
          .then(arbiters.list)
          .then(function(list) {
              vm.arbiters = list; $q.resolve(list); })
          .finally(vm.unsetLoading)
      }
    }, true);

    vm.createNewArbiter = function () {

      $uibModal.open({
        templateUrl: 'app/pages/arbiter/create/create.html',
        controller: 'ArbiterCreateController',
        controllerAs: 'arbiterCreate',
        windowClass: 'modal-success'
      })
      .result.then(function (arbiter) {
        vm.setLoading(arbiter)
          .then(arbiters.create)
          .then(arbiters.list)
          .then(function(list) {
              vm.arbiters = list; $q.resolve(list); })
          .finally(vm.unsetLoading);
      });
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }
  }
})();

