(function() { 'use strict';

    angular.module('frispo')
        .directive('frispoHeader', frispoHeader);

    /** @ngInject */
    function frispoHeader($timeout) {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/frispoHeader/frispoHeader.html',
            controller: HeaderController,
            controllerAs: 'header',
            link: HeaderLink,
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function HeaderController($scope, models, events) {

            var vm = this; vm.loadingEventInfo = false;

            // getter utilities

            vm.getUser = function() { return models.currentUser; }

            vm.getEventList = function() { return models.events || []; }

            vm.getCurrentEvent =
              function() { return models.currentEvent || null; }

            vm.getCurrentDay = function() {
              return models.currentEvent &&
                models.currentEvent.evenCurrentDay || null;
            }

            // watch for changes in models.currentEvent and load
            // event info if they are absent
            $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
              if (!vm.loadingEventInfo &&
                  currentEvent && !currentEvent.evenCurrentDay) {
                vm.loadingEventInfo = true;
                events.show(currentEvent.evenId)
                  .finally(function() { vm.loadingEventInfo = false; });
              }
            }, true);     // use object equality and not memory address

            // selection event callbacks

            vm.selectEvent = function(event) { models.currentEvent = event; }

            vm.selectCurrentMatchDay = function(day) {
              models.currentEvent.evenCurrentDay = day;
              events.update(
                models.currentEvent.evenId, { "even_day_active": day.matcDayId });
            }
        }

        function HeaderLink() {
            $timeout(function() {
                // Trigger a resize event to adjust AdminLTE height
                angular.element(window).trigger('resize');
            });
        }
    }
})();

