'use strict';

angular.module('frispo')
  /**
   * Error Manager.
   * Handles common HTTP error messages
   */
  .factory('errorManager', function (toastr, $log, $state, $q, longTermMemory, models) {
    var errorManager = function(err) {
      // log every errors
      $log.error(err);

      // Unwrap HTTP errors and take actions if needed
      if(angular.isObject(err) && err.status){
        switch(err.status){
          case 401:
            $state.go('main.logout');
            err = err.data;
            break;
          case 403:
            $state.go('main.home');
            err = err.data;
            break;
          case 422:
            err = err.data;
            break;
          case 404:
            err = err.data;
            break;
          case 413:
            err = 'Il file che stai cercando di caricare è troppo grande';
            break;
          case -1:
            err = 'Il server non è raggiungibile o non sei connesso ad Internet';
            break;
          default:
            break;
        }
      }

      var getErrorString = function (err) {
        if(angular.isString(err)){
          return err;
        }
        else if(angular.isObject(err) && (err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error)){
          return getErrorString(err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error );
        }
        else if(angular.isArray(err)){
          var myErr = [];
          for (var i = 0; i < err.length; i++) {
            myErr[i] = getErrorString(err[i]);
          }
          return getErrorString(myErr.join('<br>'));
        }
        // Don't manage different kinds of error
        else{
          longTermMemory.oblivion(); // Forget everything. We don't want the app to be broken forever!
          models.reset(); // Reset all the models. We don't want the app to be broken forever!
          return 'Si è verificato un errore imprevisto. Si prega di riprovare più tardi.';
        }
      }

      toastr.error(getErrorString(err), 'Errore');

      return $q.reject(err);
    };

    return errorManager;
  });

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('datepicker', datepicker);

  /** @ngInject */
  function datepicker() {
    var directive = {
      restrict: 'A',
      link: datepickerLink,
      scope: {
        datepicker: '@',
        model: '='
      }
    };

    return directive;

    /** @ngInject */
    function datepickerLink(scope, el) {
      scope.watchInput = function () {
        scope.$watch('model', function () {
          scope.drp.elementChanged();
        });
      }

      if(scope.datepicker === 'range'){
        scope.drp = el.daterangepicker({
          autoUpdateInput: false,
          locale: {
            format: 'DD/MM/YYYY',
            separator: ' -> '
          }
        }).data('daterangepicker');
        el.on('apply.daterangepicker', function (ev, picker) {
          scope.$apply(function () {
            scope.model = picker.startDate.format('DD/MM/YYYY') + ' -> ' + picker.endDate.format('DD/MM/YYYY');
          })
        })
        el.on('cancel.daterangepicker', function () {
          scope.$apply(function () {
            scope.model = '';
          })
        })
        scope.watchInput();
      }
      else{
        scope.drp = el.daterangepicker({
          autoUpdateInput: false,
          singleDatePicker: true,
          locale: {
            format: 'DD/MM/YYYY',
            separator: ' -> '
          }
        }).data('daterangepicker');
        el.on('apply.daterangepicker', function (ev, picker) {
          scope.$apply(function () {
            scope.model = picker.startDate.format('DD/MM/YYYY');
          })
        })
        el.on('cancel.daterangepicker', function () {
          scope.$apply(function () {
            scope.model = '';
          })
        })
        scope.watchInput();
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('can', canDirective);

  /** @ngInject */
  function canDirective($log, models) {
    var directive = {
      restrict: 'A',
      link: linkFunc,
      scope: {
        can: '@'
      }
    };

    return directive;

    function linkFunc(scope, el) {
      var watcher;
      var shown;
      var negative;
      var permission;
      scope.models = models;

      // Update the visibility of the element
      var updateVisibility = function() {
        var toShow = false;
        if(angular.isArray(models.currentUser.permissions)){ // look for permission
            for (var i = 0; i < models.currentUser.permissions.length; i++) {
              if(models.currentUser.permissions[i].permName === permission){
                toShow = true;
                break;
              }
            }
        }
        if(negative){ toShow = !toShow; } // invert the condition if needed

        // Show the element if not shown
        if(toShow && !shown){
          el.removeClass('can-hide');
          shown = true;
        }

        // Hide the element if shown
        else if(!toShow && shown){
          el.addClass('can-hide');
          shown = false;
        }
      };

      // If the action starts with "!", the action is negative
      negative = scope.can.slice(0, 1) === '!';


      // In that case, remove ! from the string
      if(negative){
        permission = scope.can.substr(1);
      }
      else{
        permission = scope.can;
      }

      // Update the visibility for the first time
      shown = true;
      updateVisibility();

      // Update the visibility as soon as the permissions change
      watcher = scope.$watch('models.currentUser.permissions', function(newVal, oldVal) {
        if(newVal && oldVal !== newVal){
          updateVisibility();
        }
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('compareTo', compareTo);

  /** @ngInject */
  function compareTo() {
    var directive = {
      restrict: 'A',
      require: 'ngModel',
      scope: {
        otherModelValue: '=compareTo'
      },
      link: compareToLink
    };

    return directive;

    /** @ngInject */
    function compareToLink(scope, element, attrs, ngModel) {
      ngModel.$validators.compareTo = function (modelvalue) {
        return modelvalue == scope.otherModelValue;
      }
      scope.$watch('otherModelValue', function () {
        ngModel.$validate();
      })
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('bootstrapMultiselect', bootstrapMultiselect);

  /** @ngInject */
  function bootstrapMultiselect() {
    var directive = {
      restrict: 'A',
      link: bootstrapMultiselectLink,
      terminal: true
    };

    return directive;

    /** @ngInject */
    function bootstrapMultiselectLink(scope, el) {
      el.multiselect();
    }
  }

})();

'use strict';

angular.module('frispo')
    /**
     * User models manager
     */
    .factory('users', function(frispoApi, errorManager, models, longTermMemory, resPipe, $state, $q, ezfb, $log, uiState, $timeout) {

        var users = {
            /**
             * Register a new user
             * @param  {Object} userData an object containing 'email' and 'password'
             * @return {Promise}         a promise with the authentication data for the user
             */
            register: function(userData) {
                return frispoApi
                    .register(userData)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(function(res) {
                        res.email = userData.email;
                        return res;
                    })
                    .then(resPipe.camelize)
                    .then(resPipe.injectUserDefaults)
                    .then(resPipe.updateCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .catch(errorManager);
            },

            /**
             * Login a user
             * @param  {Object} userData an object containing 'email' and 'password'
             * @return {Promise}         a promise with the authentication data for the user
             */
            login: function(userData) {
                return frispoApi
                    .login(userData)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(function(res) {
                        res.email = userData.email; // Add the email from the input as it's missing from the response
                        return res;
                    })
                    .then(resPipe.camelize)
                    .then(resPipe.injectUserDefaults)
                    .then(resPipe.updateCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .then(users.onLoggedIn)
                    // angular-toastr (nella configurazione in uso) non permette messaggi
                    // duplicati (e non possiamo attivarli). Per avere un feedback in caso
                    // di ripetuti tentativi di login falliti, modifichiamo il messaggio di
                    // errore ad ogni tentativo utilizzando un contatore (nascosto)
                    .catch(function(err) {
                      if (models.currentUser && !models.currentUser.errCnt)
                        models.currentUser.errCnt = 0;

                      models.currentUser.errCnt = models.currentUser.errCnt+1;
                      err.error[0].error_description =
                      "<b class='ng-hide'>"+models.currentUser.errCnt+"</b>"+err.error[0].error_description;
                      return $q.reject(err);
                    })
                    .catch(errorManager);
            },

            /**
             * Recover the password of an user
             */
            forgotPassword: function(userData) {
                return frispoApi
                    .forgotPassword(userData)
                    .then(resPipe.parseResponse)
                    .catch(errorManager)
            },

            /**
             * Logout a user
             * @param  {Object} userData an object containing the user data
             * @return {Promise}          a promise that will be fullfilled after the logout
             */
            logout: function() {
                if (models.currentUser && models.currentUser.userToken) {
                    return frispoApi
                        .logout()
                        .then(function(res) {
                            $log.info('Frispo logout');
                            return res;
                        })
                        .catch(errorManager)
                        .finally(users.onLoggedOut)
                        .then(function() {
                            ezfb.logout();
                        });
                }
                return $q.resolve();
            },

            /**
             * Login a user to frispo's server using the facebook login() response data
             * @param  {Object} facebookData The object returned by the Facebook SDK after a succesful login
             * @return {Promise}             A promise fulfilled with the authentication data for the user,
             */
            loginFacebook: function(facebookData, privacyObj) {
                return frispoApi
                    .loginFacebook(facebookData, privacyObj)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.injectUserDefaults)
                    .then(resPipe.updateCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .then(users.onLoggedIn)
                    .catch(errorManager);
            },

            /**
             * Fetch all user info
             */
            info: function() {
                var infoCalls = [
                    users.showPreferences(),
                    users.show()
                ];
                return $q.all(infoCalls);
            },

            /**
             * Show basic user info
             */
            show: function() {
                return frispoApi
                    .showUser()
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.updateMergeCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .catch(errorManager);
            },

            /**
             * Edit basic user info
             */
            update: function(userData) {
                return frispoApi
                    .updateUser(userData)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.updateMergeCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .catch(errorManager);
            },

            /**
             * Edit user info
             */
            editUser: function(userData) {
                return frispoApi
                    .editUser(userData)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.updateMergeCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .catch(errorManager);
            },

            /**
             * Delete user
             */
            deleteUser: function(userData) {
                return frispoApi
                    .deleteUser(userData)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.updateMergeCurrentUser)
                    .then(resPipe.memorizeCurrentUser)
                    .then(users.logout)
                    .catch(errorManager);
            },

            /**
             * Get the preferences of the user
             */
            showPreferences: function() {
                return frispoApi
                    .showPreferences()
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.updatePreferences)
                    .then(resPipe.memorizeCurrentUser)
                    .catch(errorManager);
            },

            /**
             * This function is used as a callback whenever a user has been succesfully logged in
             * @param  {Object} userData The object containing the user data
             * @return {Object}          The same userData, useful for promise chaining
             */
            onLoggedIn: function(userData) {
                if ($state.current.name === 'main.access') {
                    $state.go('main.admin.events');
                }
                uiState.privacyAcceptance =
                  userData.userToken === null || userData.flgPrivacy === 0;
                if (!uiState.privacyAcceptance) {
                  $log.info('Frispo login');
                  users.info();
                  uiState.loggedIn = true;
                  $state.go('main.admin.events');
                }
                return $q.resolve(userData);
            },

            /**
             * This function is used as a callback whenever a user has been succesfully logged out
             */
            onLoggedOut: function() {
                models.reset();
                longTermMemory.oblivion();
                uiState.loggedIn = false;
                if ($state.current.name !== 'main.login' && $state.current.name !== 'main.register') {
                    $timeout(function() {
                        $state.go('main.login');
                    })
                }
            },

            /**
             * Backup user data and setup the anonymous user for testing purposes
             */
            setAnonymousUser: function(res) {
                models.bkp = {
                    uuid: models.currentUser.uuid,
                    accessToken: models.currentUser.accessToken
                }
                models.currentUser.uuid = 'anonimo';
                models.currentUser.accessToken = '4e66c966e021918f0b088ddc929847f7'
                return $q.resolve(res);
            },

            /**
             * Restore user backup
             */
            unsetAnonymousUser: function(res) {
                models.currentUser.uuid = models.bkp.uuid;
                models.currentUser.accessToken = models.bkp.accessToken;
                delete models.bkp;
                return $q.resolve(res);
            },

            checkFacebookLogin: function() {
                return ezfb
                    .getLoginStatus()
                    .then(function(res) {
                        if (res.status === 'connected') {
                            return res;
                        } else {
                            return $q.reject(res);
                        }
                    });
            },

            /**
             * Configure the facebook login and events
             */
            configureFacebookCallbacks: function(privacyObj) {
                // Subscribe to FB login
                ezfb.Event.subscribe('auth.login', function(response) {
                  $log.info('Facebook login');
                  users.loginFacebook(response, privacyObj);
                });
                // Subscribe to FB logout
                ezfb.Event.subscribe('auth.logout', function() {
                    $log.info('Facebook logout');
                    if (uiState.loggedIn) {
                        users.logout();
                    }
                });
            },

            form2User: function(formData) {
                var userData = {};
                if (angular.isDefined(formData.email)) userData.email = formData.email;
                if (angular.isDefined(formData.flagMarketing)) userData.flag_marketing = formData.flagMarketing;
                if (angular.isDefined(formData.flagProfilazione)) userData.flag_profilazione = formData.flagProfilazione;
                return userData;
            },

            form2Team: function(formData) {
                var userData = {};
                if (angular.isDefined(formData.type)) userData.type = formData.type;
                if (angular.isDefined(formData.subscription)) userData.subscription = formData.subscription;
                return $q.resolve(userData);
            },

            /**
             * Buy products in cart
             */
            buyProduct: function(cart) {
                return frispoApi
                    .buyProduct(cart)
                    .then(resPipe.parseResponse)
                    .then(resPipe.unwrapArray)
                    .then(resPipe.camelize)
                    .then(resPipe.memorizeCurrentUser)
                    .catch(errorManager);
            }
        };

        return users;
    });

